.card-body {
    padding: 1rem;
}

.card-description {
    /*
    animation-duration: 1s;
    animation-name: slide-in;
    */
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    text-align: justify;
    font-size: 12px;
    padding: 1rem;
    background-color: var(--tg-theme-bg-color);
}

.dots {
    padding-top: 5px;
    display: flex;
    justify-content: center;
}

.dots div {
    background: rgba(0,26,52,.2);
    margin-left: 2px;
    border-radius: 100%;
    width: 5px;
    height: 5px;
}

.dots div:first-child {
    margin-left: 0px;
}

.dots div.active {
    background-color: var(--tg-theme-button-color);
}

@keyframes slide-in {
  from {
    translate: -100% 0;
  }

  to {
    translate: 0 0;
    scale: 100% 1;
  }
}